import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import withNavBar from "@components/nav-wrapper";
import { SERVICE_STATES } from "@utils/hooks";
import { useGetDevelopers } from "@hooks/users";
import { useParams, Link } from "react-router-dom";
// import { Link } from "react-router";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import { Container, Box, Typography } from "@material-ui/core";
import DeveloperCard from "@components/developer-card";
import MenuLeftPane from "@components/menu-left-pane";
import Grid from "@material-ui/core/Grid";
import NoView from "@components/no-view";
import ACTIONS from "@redux/actions";
import VizSensor from "react-visibility-sensor";
import Tooltip from "@material-ui/core/Tooltip";

const HtmlTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: "#f5f5f9",
    color: "rgba(0, 0, 0, 0.87)",
    maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: "1px solid black",
    marginTop: -20,
  },
}))(Tooltip);

const useStyles = makeStyles((theme) => {
  return {
    root: {
      marginTop: 50,
    },
    leftPaneGrid: {
      [theme.breakpoints.down("sm")]: {
        display: "none",
      },
    },
    developersArea: {
      marginTop: 20,
      textAlign: "center",
    },
    developerCard: {
      textAlign: "center",
      display: "inline-block",
      margin: 10,
    },
    developerAvatar: {
      border: "1px solid gray",
      borderRadius: "50%",
      width: 100,
      height: 100,
    },
  };
});

function DevelopersGrid({ type = "following" }) {
  let { developerId } = useParams();

  const classes = useStyles();
  const dispatch = useDispatch();
  const {
    getDevelopersTrigger,
    getDevelopersStatus,
    getDevelopersData,
  } = useGetDevelopers();

  const [page, setPage] = useState(1);
  const [loadingComplete, setLoadingComplete] = useState(false);
  const { developers } = useSelector((state) => {
    return {
      developers: state.developers[type],
    };
  });

  useEffect(() => {
    return function cleanUp() {
      let actionType;
      if (type === "following") {
        actionType = ACTIONS.UNLOAD_FOLLOWING_DEVELOPERS;
      } else if (type === "followers") {
        actionType = ACTIONS.UNLOAD_FOLLOWERS_DEVELOPERS;
      }

      dispatch({
        type: actionType,
        value: { [type]: [] },
      });
    };
  }, []);

  useEffect(() => {
    getDevelopersTrigger({ developerId, type, page });
  }, [page]);

  function loadMoreDevelopers(isVisible) {
    if (isVisible) setPage(page + 1);
  }

  useEffect(() => {
    if (getDevelopersStatus === SERVICE_STATES.SUCCESS) {
      if (getDevelopersData.length && !loadingComplete) {
        let actionType;
        if (type === "following") {
          actionType = ACTIONS.LOAD_FOLLOWING_DEVELOPERS;
        } else if (type === "followers") {
          actionType = ACTIONS.LOAD_FOLLOWERS_DEVELOPERS;
        }
        dispatch({
          type: actionType,
          value: { [type]: getDevelopersData },
        });
      } else {
        setLoadingComplete(true);
      }
    }
  }, [getDevelopersStatus]);

  return (
    <Container className={classes.root}>
      <Grid container spacing={3}>
        <Grid className={classes.leftPaneGrid} item xs={12} sm={3}>
          <DeveloperCard developerId={developerId} />
          <br />
          <MenuLeftPane developerId={developerId} />
        </Grid>
        <Grid className={classes.developersArea} item xs={12} sm={9}>
          <br />
          {developers.length > 0 && (
            <Box className={classes.developersArea}>
              <Typography variant="h5">
                <strong>{type}</strong>
              </Typography>

              {developers.map((developer) => {
                return (
                  <HtmlTooltip
                    key={developer.id}
                    placement="bottom"
                    title={
                      <React.Fragment>
                        <Typography>{developer.login}</Typography>
                      </React.Fragment>
                    }
                  >
                    <Box className={classes.developerCard}>
                      <Link to={"/developer/" + developer.login}>
                        <img
                          className={classes.developerAvatar}
                          alt={developer.name}
                          src={developer.avatar_url}
                        />
                      </Link>
                    </Box>
                  </HtmlTooltip>
                );
              })}
              {!loadingComplete && (
                <div>
                  <VizSensor
                    onChange={(isVisible) => {
                      loadMoreDevelopers(isVisible);
                    }}
                  >
                    <div>Loading ...</div>
                  </VizSensor>
                </div>
              )}
            </Box>
          )}
          {loadingComplete && !developers.length && (
            <NoView primaryText="No developers" />
          )}
        </Grid>
      </Grid>
    </Container>
  );
}

export default withNavBar(DevelopersGrid);
