import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import withNavBar from "@components/nav-wrapper";
import { useParams } from "react-router-dom";

import { SERVICE_STATES } from "@utils/hooks";
import { useGetSnippets, UseGetDeveloperSnippets } from "@hooks/snippets";
import Grid from "@material-ui/core/Grid";

import AlertMessage from "@components/alert-message";
import CreateSnippet from "@components/create-snippet";
import { Box, Container } from "@material-ui/core";
import SnippetCardSkeleton from "@components/skeletons/snippet-card-skeleton";
import DeveloperCard from "@components/developer-card";
import MenuLeftPane from "@components/menu-left-pane";
import Snippets from "@components/snippets";
import ACTIONS from "@redux/actions";
import { useSelector, useDispatch } from "react-redux";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 50,
    textAlign: "center",
  },
  leftPaneGrid: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
  snippetsArea: {},
}));

function Developer() {
  let { developerId } = useParams();
  const classes = useStyles();
  const dispatch = useDispatch();

  const { snippets, userId } = useSelector((state) => {
    return {
      snippets: state.snippets,
      userId: state.profile.user.login,
    };
  });

  const [showAlert, setShowAlert] = useState(false);
  const [alertInfo, setAlertInfo] = useState({});
  const {
    getSnippetsTrigger,
    getSnippetsStatus,
    getSnippetsData,
  } = useGetSnippets();
  const {
    getDeveloperSnippetsTrigger,
    getDeveloperSnippetsStatus,
    getDeveloperSnippetsData,
  } = UseGetDeveloperSnippets();

  useEffect(() => {
    if (userId === developerId) {
      getSnippetsTrigger();
    } else {
      getDeveloperSnippetsTrigger({ developerId });
    }
    return function cleanup() {
      dispatch({
        type: ACTIONS.UNLOAD_SNIPPETS,
      });
    };
  }, [developerId]);

  useEffect(() => {
    if (getSnippetsStatus === SERVICE_STATES.SUCCESS) {
      dispatch({
        type: ACTIONS.LOAD_SNIPPETS,
        value: getSnippetsData,
      });
    }
  }, [getSnippetsStatus]);

  useEffect(() => {
    if (getDeveloperSnippetsStatus === SERVICE_STATES.SUCCESS) {
      dispatch({
        type: ACTIONS.LOAD_SNIPPETS,
        value: getDeveloperSnippetsData,
      });
    }
  }, [getDeveloperSnippetsStatus]);

  function handleAlertCallback(alertInfo) {
    setAlertInfo(alertInfo);
    setShowAlert(true);
  }

  function handleCloseAlert() {
    setShowAlert(false);
  }

  function handleRefreshSnippetsCallback() {
    getSnippetsTrigger();
  }

  return (
    <Container className={classes.root}>
      <Grid container spacing={3}>
        <Grid className={classes.leftPaneGrid} item xs={12} sm={3}>
          <DeveloperCard developerId={developerId} />
          <br />
          <MenuLeftPane developerId={developerId} />
        </Grid>
        <Grid item xs={12} sm={9}>
          {userId === developerId && (
            <CreateSnippet
              refreshSnippetsCallback={handleRefreshSnippetsCallback}
              alertCallback={handleAlertCallback}
            />
          )}

          {!snippets && (
            <Box>
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
              <SnippetCardSkeleton />
            </Box>
          )}
          <Snippets isUser={userId === developerId} snippets={snippets} />

          {showAlert && (
            <AlertMessage handleCloseAlert={handleCloseAlert} {...alertInfo} />
          )}
        </Grid>
      </Grid>
    </Container>
  );
}

export default withNavBar(Developer);
